<template>
    <div>
        <div class="admin-list-pass" v-if="!hideFilter && !releaseType ">
            <el-select @change="fetchData()" width="120" v-if="!hidePass" size="small" v-model="isPass" placeholder="">
                <el-option
                    v-for="(val, key) in options"
                    :key="val"
                    :label="key"
                    :value="val">
                </el-option>
            </el-select>
            <el-input class="pick-erpai" size="small" v-model="factoryName">
                <template slot="prepend">:</template>
            </el-input>
            <el-input v-if="!hideEartagFilter" class="pick-erpai" size="small" v-model="eartag">
                <template slot="prepend">:</template>
            </el-input>
            <el-date-picker
                size="small"
                v-model="gmtCreate"
                type="datetimerange"
                range-separator=""
                start-placeholder=""
                end-placeholder=""
                format="yyyy-MM-dd"
                value-format="yyyy-MM-dd"
                align="right">
            </el-date-picker>
            <el-button @click="fetchData()" size="small" type="primary"></el-button>
            <el-button @click="export2xls()" size="small" type="primary" icon="el-icon-download"></el-button>
        </div>
        <el-table
            v-loading="load"
            ref="table"
            tooltip-effect="dark"
            class="admin-table"
            :data="tableData">
            <template v-if="hasCommonHeader">
                <el-table-column
                    align='center'
                    width="150"
                    prop="ispassCheck"
                    label="">
                </el-table-column>
                <el-table-column
                    align='center'
                    width="150"
                    prop="factoryName"
                    label="">
                </el-table-column>
                <el-table-column
                    align='center'
                    width="200"
                    prop="gmtCreate"
                    label="">
                </el-table-column>
            </template>
            <el-table-column
                show-overflow-tooltip
                v-for="(th, i) in headers"
                :key="i"
                align='center'
                :prop="th.prop"
                :label="th.label"
                :width="th.width || 150">
                <el-table-column
                    v-if="th.children"
                    v-for="(thc, j) in th.children"
                    :key="j"
                    :prop="thc.prop"
                    :label="thc.label"
                    :width="thc.width || 150">
                </el-table-column>
            </el-table-column>
            <template v-if="hasCommonTailHeader">
                <el-table-column
                    align='center'
                    width="150"
                    prop="operatorName"
                    label="">
                </el-table-column>
                <el-table-column
                    align='center'
                    width="150"
                    :prop="isProName ? 'professorName' : 'professor'"
                    label="">
                </el-table-column>
                <el-table-column
                    align='center'
                    width="150"
                    :prop="isProName ? 'supervisorName' : 'supervisor'"
                    label="">
                </el-table-column>
            </template>
            <el-table-column
                width="150"
                align='center'
                v-if="hasUnpass"
                prop="upassReason"
                label="">
            </el-table-column>
            <el-table-column
                v-if="hasSup"
                align='center'
                prop="ispassSup"
                label="">
            </el-table-column>
            <el-table-column
                class="action"
                fixed="right"
                label=""
                align='center'
                width="160">
                <template slot-scope="scope">
                    <div class="opr" v-if="!releaseType && !isCheck">
                        <span v-if="!hideView" @click="edit(scope.$index, 1)"></span>
                        <template>
                            <span @click="edit(scope.$index)" v-if="showEdit"></span>
                            <span @click="deleteItem(scope.$index)"></span>
                        </template>
                    </div>
                    <div class="opr" v-else-if="releaseType">
                        <span  @click="viewPlan(scope.$index)"></span>
                    </div>
                    <div class="opr" v-else>
                        <span @click="Spv(1, scope.$index)">{{isSpv ? '' : ''}}</span>
                        <span v-if="!isSpv" @click="Spv(0, scope.$index)"></span>
                    </div>
                </template>
            </el-table-column>
        </el-table>

        <el-pagination
            layout="prev, pager, next"
            :total="total"
            @current-change="fetchData"
            :current-page.sync="page">
        </el-pagination>
    </div>
</template>

<script>
import XLSX from 'xlsx'
import { isReqSuccessful } from '@/util/jskit'
import { getUserById, getReleaseByName } from '@/util/getdata'
import {
// 
    patchWelfare,
    patchBreeding,
    patchPrevention,
    patchDisinfect,
    patchImmune,
    patchAntiscolic,
    patchStage,
// 
    patchProWelfare,
    patchProPrevention,
    patchProBreeding,
    patchProDisinfect,
    patchProImmune,
    patchProAntiscolic,
    patchProStage
} from '@/util/getdata'


export default {
    props: {
        // 
        hideView: {
            type: Boolean,
            default: false
        },
        // 
        hideFilter: {
            type: Boolean,
            default: false
        },
        // 
        modpath: {
            type: String
        },
        getData: {
            type: Function
        },
        deleteData: {
            type: Function,
            default () {
                return () => {}
            }
        },
        // 
        isCheck: {
            type: Boolean,
            default: false
        },
        headers: {
            type: Array
        },


        // 
        hidePass: {
            type: Boolean,
            default: false
        },
        // prac
        noPrac: {
            type: Boolean,
            default: false
        },
        // 
        isAgent: {
            type: Boolean,
            default: false
        },
        // 
        releaseType: {
            type: String,
            default: ''
        },
        showEdit: {
            type: Boolean,
            default: true
        },
        hideEartagFilter: {
            type: Boolean,
            default: false
        },
        checkModule: {
            type: String
        },
        // 
        hasSup: {
            type: Boolean,
            default: true
        },
        // 
        hasCommonTailHeader: {
            type: Boolean,
            default: false
        },
        // 
        hasCommonHeader: {
            type: Boolean,
            default: false
        },
        // 
        hasUnpass: {
            type: Boolean,
            default: true
        }
    },

    watch: {
        checkModule (newM) {
            this.isProName = ['prevention', 'nutrition/stage', 'welfare', 'nutrition/breed'].includes(newM)
        },

        getData (newV) {
            this.fetchData()
        }
    },

    mounted () {
        this.isProName = ['prevention', 'nutrition/stage', 'welfare', 'nutrition/breed'].includes(this.modpath)
        let id = this.$route.params.id
        getUserById(id).then(res => {
            if (isReqSuccessful(res)) {
                this.user = res.data.model
                this.isSpv = res.data.model.userRole === 20
            }
        }).then(this.fetchData)
    },

    data () {
        return {
            isSpv: false, // 
            isProName: false,
            load: true, // loading
            page: 1, // 
            total: 10, // 
            tableData: [], // 

            isPass: null, // -
            factoryName: null, // -
            options: { // 
                : null,
                : 0,
                : 1,
                : 2
            },

            eartag: null,
            gmtCreate: null
        }
    },

    methods: {
        //  xls 
        export2xls () {
            if (!this.tableData.length) {
                this.$message.warning('')
            }

            let s2ab = s => {
                if (typeof ArrayBuffer !== 'undefined') {
                    var buf = new ArrayBuffer(s.length)
                    var view = new Uint8Array(buf)
                    for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xFF
                    return buf
                } else {
                    var buf = new Array(s.length)
                    for (var i = 0; i != s.length; ++i) buf[i] = s.charCodeAt(i) & 0xFF
                    return buf
                }
            }

            let eBody = this.tableData.slice(0)
            eBody.forEach(v => {
                Object.keys(v).forEach(vk => {
                    let inTable = false
                    //  children 
                    this.headers.forEach(vh => {
                        if (vh.children) {
                            inTable = vh.children.find(v => v.prop === vk)
                        }
                    })

                    if (!inTable) {
                        inTable = this.headers.find(v => v.prop === vk)
                    }
                    if (inTable && inTable.label) {
                        v[inTable.label] = v[vk]
                    }

                    // this.header  factoryName 
                    if (!['ispassCheck', 'factoryName', 'gmtCreate', 'remark', 'ispassSup', 'operatorName', 'professorName', 'upassReason', 'supervisorName'].includes(vk)) {
                        delete v[vk]
                    } else {
                        let map = {
                            ispassCheck: '',
                            factoryName: '',
                            gmtCreate: '',
                            remark: '',
                            ispassSup: '',
                            operatorName: '',
                            professorName: '',
                            upassReason: '',
                            supervisorName: ''
                        }
                        v[map[vk]] = v[vk]
                        delete v[vk]
                    }
                })
            })
            // console.log(eBody)
            const wb = { SheetNames: ['Sheet1'], Sheets: {}, Props: {} }
            const wopts = { bookType: 'biff8', bookSST: false, type: 'binary' }
            wb.Sheets['Sheet1'] = XLSX.utils.json_to_sheet(eBody)

            let tmpa = document.createElement("a");
            let obj = new Blob([s2ab(XLSX.write(wb, wopts))], { type: 'application/octet-stream' })
            tmpa.download = '.xls'
            tmpa.href = URL.createObjectURL(obj) //a
            tmpa.click() //
            setTimeout(function () { //
                URL.revokeObjectURL(obj) //URL.revokeObjectURL()object URL
            }, 100)
        },

        async Spv (isPass, idx) {
            try {
                let result
                if (isPass === 0) {
                    result = await this.$prompt('', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        inputPattern: /.+/,
                        inputErrorMessage: ''
                    })
                }
                let {id, ispassCheck, ispassSup} = this.tableData[idx]
                let superviseMap = {
                    welfare: patchWelfare,
                    prevention: patchPrevention,
                    'nutrition/breed': patchBreeding,
                    'nutrition/stage': patchStage,
                    'health/antiscolic': patchAntiscolic,
                    'health/disinfect': patchDisinfect,
                    'health/immune': patchImmune
                }
                let professorMap = {
                    welfare: patchProWelfare,
                    prevention: patchProPrevention,
                    'nutrition/breed': patchProBreeding,
                    'nutrition/stage': patchProStage,
                    'health/antiscolic': patchProAntiscolic,
                    'health/disinfect': patchProDisinfect,
                    'health/immune': patchProImmune
                }
                let unpassReason = result && result.value
                let data = {
                    unpassReason: unpassReason || '',
                    factoryNum: this.user.userFactory,
                    name: this.user.userRealname
                }

                // userRole 20
                if (this.user.userRole == 20) {
                    if (ispassSup === '') {
                        this.$message.warning('')
                        return
                    }
                    data.supervisor = +this.$route.params.id

                    data.ispassSup = isPass
                    delete data.unpassReason
                    superviseMap[this.checkModule](id, data).then(res => {
                        if (isReqSuccessful(res)) {
                            this.$message.success('')
                            this.tableData[idx].ispassSup = isPass ? '' : ''
                            this.$set(this.tableData[idx], 'supervisor', this.user.userRealname)
                            this.$set(this.tableData[idx], 'supervisorName', this.user.userRealname)
                        }
                    }, _ => {
                        this.$message.error('')
                    })
                } else {
                    if (ispassCheck !== '') {
                        this.$message.warning('')
                        return
                    }
                    data.professor = +this.$route.params.id

                    data.ispassCheck = isPass
                    professorMap[this.checkModule](id, data).then(res => {
                        if (isReqSuccessful(res)) {
                            this.$message.success('')
                            this.tableData[idx].ispassCheck = isPass ? '' : ''
                            this.tableData[idx].professor = this.user.userRealname
                            this.tableData[idx].professorName = this.user.userRealname
                            if (isPass === 0) {
                                this.$set(this.tableData[idx], 'upassReason', unpassReason)
                            }
                        }
                    }, _ => {
                        this.$message.error('')
                    })
                }
            } catch(e) {
                e.message && console.log(e.message)
                return false
            }
        },

        viewPlan (index) {
            let id = this.tableData[index].id
            this.$router.push({name: this.modpath, query: {view: id}})
        },

        async fetchData () {
            let param = {
                page: this.page - 1,
                size: 10
            }
            if (this.isPass !== null) {
                param.ispassCheck = this.isPass
            }
            if (this.factoryName !== null) {
                param.factoryName = this.factoryName
            }
            if (this.gmtCreate !== null) {
                console.log(this.gmtCreate)
                param.startTime = this.gmtCreate[0]
                param.endTime = this.gmtCreate[1]
            }

            let pathid
            let { userFactory, userRealname, id, factoryName } = this.user
            //   
            if (userFactory !== undefined) {
                pathid = userFactory
            } else if (id !== undefined) {
                pathid = id
            }

            this.load = true
            if (!this.releaseType) {
                this.getData(pathid, param).then(res => {
                    if (isReqSuccessful(res)) {
                        let data = res.data

                        // 
                        if (this.checkModule === 'nutrition/breed') {
                            data.List.forEach(v => {
                                v.upassReason = v.professorNotPassReason
                                delete v.professorNotPassReason
                            })
                        }

                        if (this.isAgent) {
                            data.List.forEach(v => {
                                let map = ['', '', '', '']
                                v.agentRank = map[v.agentRank]
                            })
                        }
                        let item = data.List[0]
                        if (item && item.ispassCheck !== null && item.ispassCheck !== undefined) {
                            data.List.forEach(v => {
                                let map = ['', '', '']
                                v.ispassCheck = map[v.ispassCheck]
                            })
                        }
                        if (item && item.ispassSup !== null && item.ispassSup !== undefined) {
                            data.List.forEach(v => {
                                let map = ['', '', '']
                                v.ispassSup = map[v.ispassSup]
                            })
                        }
                        if (item && item.killWormDeratization !== undefined) {
                            data.List.forEach(v => {
                                let map = ['', '']
                                Object.keys(v).forEach(v2 => {
                                    if (v[v2] === 0 || v[v2] === 1) {
                                        v[v2] = map[v[v2]]
                                    }
                                })
                            })
                        }
                        if (item && item.materialA !== undefined) {
                            data.List.forEach(v => {
                                let map = [
                                    'materialA',
                                    'materialM',
                                    'materialO',
                                    'materialWM',
                                    'materialWO',
                                    'roughageP',
                                    'roughageD',
                                    'roughageO',
                                    'roughageWP',
                                    'roughageWD',
                                    'roughageWO',
                                    'pickingM',
                                    'pickingR',
                                    'pickingO'
                                ]
                                map.forEach(v2 => {
                                    if (v[v2] && v[v2].indexOf('[') !== -1) {
                                        v[v2] = JSON.parse(v[v2]).join(',')
                                    }
                                })
                            })
                        }
                        this.tableData = data.List
                        this.total = data.size
                    }
                    this.load = false
                }, _ => {
                    this.load = false
                    this.$message.error('')
                })
            } else {
                let res = await getReleaseByName(this.releaseType)
                this.tableData = res.data.List
                this.total = res.data.size
                this.load = false
            }
        },

        edit (index, isView) {
            let id = this.tableData[index].id
            let path
            let pathid = this.$route.params.id
            if (this.noPrac) {
                if (isView) {
                    path = `/admin/${pathid}/${this.modpath}?view=${id}`
                } else {
                    path = `/admin/${pathid}/${this.modpath}?edit=${id}`
                }
            } else if (!this.isCheck) {
                if (isView) {
                    path = `/admin/${pathid}/${this.modpath}/prac?view=${id}`
                } else {
                    path = `/admin/${pathid}/${this.modpath}/prac?edit=${id}`
                }
            } else {
                path = `/admin/${pathid}/${this.checkModule}/prac?check=${id}`
            }
            this.$router.push(path)
        },

        deleteItem (index) {
            let { id, ispassCheck } = this.tableData[index]
            if (ispassCheck && ispassCheck === '') {
                this.$message.warning('')
                return
            }
            this.$confirm(', ?', '', {
                type: 'warning'
            }).then(() => {
                this.deleteData(id).then(res => {
                    if (isReqSuccessful(res)) {
                        this.fetchData()
                        this.$message.success('!')
                    }
                })
            }).catch(() => {
                return false
            })
        }
    }
}
</script>

<style lang="stylus">
@import '~@/assets/css/color'
.el-table th
    border-left 2px solid #98c9e6
    color #fff
    background-color color-main !important
</style>



