/**
 * el-select
 */
export const getMedicines = (q, cb) => {
    let medicines = [
        {value: '', key: 0},
        {value: '', key: 1},
        {value: '', key: 2},
        {value: '', key: 3},
        {value: '', key: 4},
        {value: '', key: 5}
    ]
    cb(medicines)
}

export const getWays = (q, cb) => {
    let ways = [
        {value: '', key: 0},
        {value: '', key: 1},
        {value: '', key: 2},
        {value: '', key: 3}
    ]
    cb(ways)
}

export const getDoses = (q, cb) => {
    let doses = [
        {value: '0.3mg/kg', key: 0},
        {value: '4mg/kg', key: 1},
        {value: '35mg/kg', key: 2},
        {value: '1000', key: 3},
        {value: '500', key: 4}
    ]
    cb(doses)
}

